const App = () => {

    const[showMenu, setShowMenu] = useState(false)

    document.title ="Portfolio"



    let menu;

    if(showMenu) {
        menu =
            <div>
                <button className="absolute bg-gray-400 top-12 right-0 shadow z-10 rounded-lg">
                    <Link className="font-2xl px-2 py-2 flex font-bold" onClick={() => setShowMenu(!showMenu)} to="/">Home</Link>
                    <Link className="font-2xl px-2 py-2 flex font-bold" onClick={() => setShowMenu(!showMenu)} to="/yeetyourself">Yeet Yourself</Link>
                    <Link className="font-2xl px-2 py-2 flex font-bold" onClick={() => setShowMenu(!showMenu)} to="/spaceinvaders">Space Invaders</Link>
                    <Link className="font-2xl px-2 py-2 flex font-bold" onClick={() => setShowMenu(!showMenu)} to="/proceduralgeneration">Procedural Generation</Link>
                </button>
            </div>
    }

    return (
        <div className="flex flex-col h-screen">
            <Router>
                <header className="bg-gray-800 flex items-center justify-between px-4 py-3 left-0 right-0">
                    <img className="right-0 w-12 fixed block" alt="burger" src={hamburgermenuicon} onClick={() => setShowMenu(!showMenu)}/>
                    <Link className="text-4xl font-bold text-gray-200" to="/">Portfolio: Miika Haukkala</Link>
                </header>

                {menu}

                    <Switch>
                        <Route path="/spaceinvaders">
                            <div className="relative container mx-auto xl:w-3/5">
                                <SpaceInvaders />
                            </div>
                        </Route>
                        <Route path="/proceduralgeneration">
                            <div className="relative container mx-auto xl:w-3/5">
                                <ProceduralGeneration />
                            </div>
                        </Route>
                        <Route path="/yeetyourself">
                            <div className="relative container mx-auto xl:w-3/5">
                                <YeetYourself />
                            </div>
                        </Route>
                        <Route path="/">
                            <div className="relative container mx-auto xl:w-3/5">
                                <Home />
                            </div>
                        </Route>

                    </Switch>
                </Router>

                <div>
                    <i className="relative bottom-0 text-xl">Made by Miika Haukkala</i>
                </div>

        </div>


    )
}

ReactDOM.render(<App />, document.getElementById('root'))